import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useAuthStore } from '../stores/authStore';
import { useCategoryStore } from '../stores/categoryStore';
import { CategoryEditor } from '../components/features/CategoryEditor';
import { UI } from '@shared-constants/ui';

/**
 * Pagina de opiuni a aplicaiei
 * Conine setri i configurri pentru utilizator, inclusiv gestionarea categoriilor
 */
const OptionsPage: React.FC = () => {
  const { user, logout } = useAuthStore();
  const navigate = useNavigate();
  const categoryStore = useCategoryStore();
  const [showCategoryEditor, setShowCategoryEditor] = React.useState(false);
  
  // IMPORTANT: Folosim o referin pentru a ine minte starea anterioar a editorului
  // i pentru a preveni buclele infinite, conform memoriei e0d0698c
  const prevEditorStateRef = React.useRef(showCategoryEditor);

  // Dac utilizatorul nu este autentificat, afim un mesaj
  if (!user) {
    return (
      <div className="container mx-auto px-token py-token-xl" data-testid="options-page-not-logged">
        <h1 className="text-2xl font-bold text-primary-700 mb-token">{UI.OPTIONS_PAGE_TITLE || 'Opiuni'}</h1>
        <div className="bg-warning-100 border-l-4 border-warning-500 text-warning-700 p-token mb-token" role="alert">
          <p>{UI.LOGIN_REQUIRED || 'Trebuie s fii autentificat pentru a accesa aceast pagin.'}</p>
        </div>
      </div>
    );
  }

  // Funcie pentru gestionarea logout-ului
  const handleLogout = async () => {
    try {
      await logout();
      navigate('/login'); // Redirecionare la pagina de login dup logout
    } catch (error) {
      console.error('Logout failed:', error);
      // Aici ai putea afia un mesaj de eroare pentru utilizator, dac este cazul
    }
  };

  return (
    <div className="container mx-auto px-token py-token-xl" data-testid="options-page">
      <h1 className="text-2xl font-bold text-primary-700 mb-token">{UI.OPTIONS_PAGE_TITLE || 'Opiuni'}</h1>
      
      {/* Seciunea de gestionare categorii */}
      <div className="bg-secondary-50 shadow-token rounded-token-lg p-token-lg mb-token-lg">
        <h2 className="text-xl font-semibold text-primary-600 mb-token">{UI.CATEGORY_MANAGEMENT || 'Gestionare categorii'}</h2>
        <p className="mb-token">{UI.CATEGORY_MANAGEMENT_DESCRIPTION || 'Personalizai categoriile i subcategoriile pentru a se potrivi nevoilor dvs. specifice de bugetare.'}</p>
        
        <button
          onClick={() => setShowCategoryEditor(true)}
          className="btn btn-primary"
          data-testid="open-category-editor-btn"
        >
          {UI.MANAGE_CATEGORIES || 'Gestionare categorii'}
        </button>
      </div>

      {/* Alte seciuni de opiuni pot fi adugate aici */}
      <div className="bg-secondary-50 shadow-token rounded-token-lg p-token-lg mb-token-lg">
        <h2 className="text-xl font-semibold text-primary-600 mb-token">{UI.DISPLAY_OPTIONS || 'Opiuni de afiare'}</h2>
        <p className="text-secondary-600">{UI.COMING_SOON || 'n curnd'}</p>
      </div>

      <div className="bg-secondary-50 shadow-token rounded-token-lg p-token-lg mb-token-lg">
        <h2 className="text-xl font-semibold text-primary-600 mb-token">{UI.DATA_EXPORT || 'Export date'}</h2>
        <p className="text-secondary-600">{UI.COMING_SOON || 'n curnd'}</p>
      </div>

      {/* Seciunea Cont Utilizator - NOU */}
      <div className="bg-secondary-50 shadow-token rounded-token-lg p-token-lg mb-token-lg">
        <h2 className="text-xl font-semibold text-primary-600 mb-token">{UI.ACCOUNT_SETTINGS || 'Setri Cont'}</h2>
        <p className="mb-token text-neutral-600">{UI.ACCOUNT_LOGOUT_DESCRIPTION || 'Deconecteaz-te de la contul tu.'}</p>
        <button
          onClick={handleLogout}
          className="btn btn-danger" // Stil pentru logout, asigur-te c .btn-danger este definit n index.css
          data-testid="logout-btn"
        >
          {UI.LOGOUT_BUTTON || 'Logout'}
        </button>
      </div>

      {/* Modal pentru editarea categoriilor */}
      <CategoryEditor
        open={showCategoryEditor}
        onClose={() => {
          // Salvm starea anterioar nainte de a o modifica
          prevEditorStateRef.current = showCategoryEditor;
          setShowCategoryEditor(false);
          
          // IMPORTANT: n loc s folosim evenimente care pot cauza bucle infinite,
          // salvm un timestamp n localStorage pentru a notifica alte componente
          // despre schimbrile n categorii. Aceast abordare respect recomandrile
          // din memoria critic d7b6eb4b-0702-4b0a-b074-3915547a2544 i pattern-urile
          // din memoria e0d0698c-ac6d-444f-8811-b1a3936df71b
          console.log('[OptionsPage] Notificarea componentelor despre schimbri n categorii');
          const timestamp = Date.now();
          localStorage.setItem('budget-app-last-category-update', timestamp.toString());
          console.log('[OptionsPage] Timestamp salvat:', new Date(timestamp).toISOString());
        }}
        userId={user.id}
      />
    </div>
  );
};

export default OptionsPage;
